import React from 'react'
import { Box } from 'theme-ui'
import { Layout, Stack, Hero, Main, PreFooter } from '@layout'
import CardList from '@components/CardList'
import Divider from '@components/Divider'
import Pagination from '@components/Pagination'
import NewsletterExpanded from '@widgets/NewsletterExpanded'
import Seo from '@widgets/Seo'
import Categories from '@widgets/Categories'
import { useBlogCategories } from '@helpers-blog'
import HeroComponent from '../components/Hero/Hero'

import { Helmet } from "react-helmet";
import CookieConsent, { getCookieConsentValue } from 'react-cookie-consent';
import { useState, useEffect } from "react"
const HEAP_APPID = '2162541267'

const Posts = ({
  data: { featuredPosts = {}, paginatedPosts = {}, posts = {} },
  ...props
}) => {
  const { pageContext: { services = {}, basePath } = {} } = props
  const categories = useBlogCategories()
  const sliderRef = React.useRef()

  // loading heap, simple react effect which will fire on page load as well as when state updated
  const [loadHeap, setLoadHeap] = useState(0);
  useEffect(() => {
      if (getCookieConsentValue() && window.heap !== undefined && window.heap.userId === undefined) {
        window.heap.load(HEAP_APPID);
      }
  });

  return (
    <Layout {...props}>
      <Seo title='Home' />

      <Helmet>
        <script type="text/javascript">{`
          window.heap=window.heap||[],heap.load=function(e,t){window.heap.appid=e,window.heap.config=t=t||{};var r=document.createElement("script");r.type="text/javascript",r.async=!0,r.src="https://cdn.heapanalytics.com/js/heap-"+e+".js";var a=document.getElementsByTagName("script")[0];a.parentNode.insertBefore(r,a);for(var n=function(e){return function(){heap.push([e].concat(Array.prototype.slice.call(arguments,0)))}},p=["addEventProperties","addUserProperties","clearEventProperties","identify","resetIdentity","removeEventProperty","setEventProperties","track","unsetEventProperty"],o=0;o<p.length;o++)heap[p[o]]=n(p[o])};   
        `}</script>
      </Helmet>
      <CookieConsent
        location="bottom"
        buttonText="Accept"
        enableDeclineButton={false}
        declineButtonText="Decline"
        style={{ background: "#115571" }}
        buttonStyle={{ background: "#7CBAB7" }}
        onAccept={() => {
          setLoadHeap(loadHeap + 1);
        }}>
        This website uses cookies to track user interactions and improve user experience.
      </CookieConsent>

      <Hero
        pt={4}
        pb={5}
        sx={{
          background: t =>
            `linear-gradient(
              0deg,
              ${t.colors.omegaLighter},
              ${t.colors.background}
            )`
        }}
      >
        <Divider space={0} />
        <Box sx={{ position: `relative`, zIndex: 3 }}>
          <CardList
            nodes={featuredPosts.nodes}
            variant={['horizontal-hero']}
            limit={3}
            omitFooter
            slider
            autoPlay
            fade
            arrows={false}
            controlPosition='bottom'
            ref={sliderRef}
            loading='eager'
          />
          <Box sx={{ display: [`none`, null, `block`] }}>
            <Divider space={1} />
            <CardList
              nodes={featuredPosts.nodes}
              variant={['horizontal-md', 'horizontal-aside']}
              limit={3}
              columns={[1, 0, 3]}
              omitCategory
              omitFooter
              asNavFor={sliderRef}
              loading='eager'
            />
          </Box>
          <Divider space={1} />
          <Box sx={{ display: [`none`, `block`] }}>
            <Categories
              categories={categories}
              variant='horizontal'
              omitTitle
            />
          </Box>
        </Box>
      </Hero>

      <Stack>
        <Main sx={{ zIndex: 1 }}>
          <CardList
            variant={['horizontal']}
            title='Recents Posts'
            limit={8}
            nodes={paginatedPosts.nodes}
            columns={[1, 2]}
            omitMedia
          />
        </Main>
      </Stack>
      <Divider />
      <PreFooter>
        <Pagination {...paginatedPosts.pageInfo} basePath={basePath} />
      </PreFooter>
      <Divider space={1} />
      <Stack>
        <Main sx={{ zIndex: 1 }}>
          {services.mailchimp && <NewsletterExpanded simple />}
        </Main>
      </Stack>
    </Layout>
  )
}

export default Posts
